$mainColor: #000000;

// 3. class로 애니메이션 넣기
.wow-enter {
  // 애니메이션 시작 때 적용할 CSS
  opacity: 0;
}
.wow-enter-active {
  // 애니메이션 동작 때 적용할 CSS
  opacity: 1;
  transition: all 500ms;
}

.red {
  color: $mainColor;
}

// 5. 함수는 @mixin/ @include
@mixin 함수() {
  background-color: #eeeeee;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.my-alert {
  @include 함수();
}

// 4. 복붙하지 말고 @extend
.my-alert2 {
  @extend .my-alert;
  background-color: #ffe591;
}

.my-alert p {
  margin-bottom: 0;
}

.watchedItem {
  background-color: cadetblue;
  width: 150px;
  padding: 15px;
  position: absolute;
  top: 10%;
  right: 5%;
  h6 {
    font-weight: bold;
    color: white;
    background-color: gray;
    padding: 10px;
  }
  img {
    width: 95%;
    margin-top: 10px;
  }
}
